import { BuildContext, Component, HtmlElement, StatelessComponent } from "@equantic/runtime";
import { Dictionary } from "./Dictionary";
import { DynamicElement } from "./DynamicElement";
import { Text } from "./Text";

export class Button extends StatelessComponent {
    build(context: BuildContext) {
        let theme = context.getService('eQuantic.UI.Core.Theme.IAppTheme');let buttonTheme = theme != null ? theme.button : null;let baseStyle = '';if (buttonTheme != null) baseStyle = buttonTheme.base;let variantStyle = '';if (buttonTheme != null && buttonTheme.variants != null && this.variant != null) {let v = buttonTheme.variants[this.variant];if (v != null) variantStyle = v;}let attrs = { ["type"]: this.type, ["class"]: baseStyle + ' ' + variantStyle + ' ' + (this.className != null ? this.className : '') };if (this.disabled) attrs['disabled'] = 'true';let element = new DynamicElement({ tagName: 'button', customAttributes: attrs, customEvents: this.buildEvents() });if ((this.children.length > 0)) {for (const child of this.children) {element.children.push(child);}} else if (this.text != null) {element.children.push(new Text(this.text));}return element;
    }

}

