import { BuildContext, Component, HtmlElement } from "@equantic/runtime";

export class DynamicElement extends HtmlElement {
    render() {
        let children = this.children.map((c) => c.render());if (!(!this.innerText || this.innerText === '')) {children.splice(0, 0, { tag: '#text', textContent: this.innerText });}return { tag: this.tagName, attributes: this.customAttributes, events: this.customEvents, children: children };
    }

}

