import { BuildContext, Component, HtmlElement, HtmlStyle } from "@equantic/runtime";

export class Flex extends HtmlElement {
    render() {
        let attrs = this.buildAttributes();let layoutStyle = new HtmlStyle({ display: 'flex', flexDirection: this.direction, justifyContent: this.justify, alignItems: this.align, gap: this.gap, flexWrap: this.wrap ? 'wrap' : 'noWrap' });let s = attrs['style'];let existingStyle = s != null ? s + '; ' : '';attrs['style'] = existingStyle + layoutStyle.toCssString();return { tag: 'div', attributes: attrs, events: this.buildEvents(), children: this.children.map((c) => c.render()) };
    }

}

